.livechat {
  position: fixed;
  z-index: 10000;
  display: none;
  width: subtract(100vw, $livechat-margin * 2);
  max-width: $livechat-max-width;
  height: subtract(100vh, $livechat-margin * 2);
  max-height: $livechat-max-height;
  margin: $livechat-margin;
  background-color: $livechat-bg;
  @include border-radius(0 $livechat-border-radius $livechat-border-radius);
  box-shadow: 0 0 5px 2px rgba(204, 204, 204, 1);

  &.in {
    right: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
  }

  iframe {
    width: 100%;
    height: 100%;
    border: 0;
    @include border-radius(0 0 $livechat-border-radius $livechat-border-radius);
  }
}

.livechat-header {
  padding: $livechat-header-padding-y $livechat-header-padding-x;
}

.livechat-body {
  display: flex;
  flex-grow: 1;
  height: 100%;
}

.livechat-start-button {
  position: fixed;
  z-index: 10000;
  display: none;
  cursor: pointer;

  &.in {
    right: 0;
    bottom: 0;
    display: block;
  }
}
