@use "sass:math";

@import "bootstrap/scss/alert";

@each $state, $value in $theme-colors {
  $alert-background: $value;
  $alert-border: $value;
  $alert-color: $white;

  @if ($state == "light") {
    $alert-color: mix($value, color-contrast($alert-background), math.abs($alert-color-scale));
    $alert-background: $alert-light-bg;
  }
  @if ($state == "tertiary") {
    $alert-color: $body-color;
  }

  .alert-#{$state} {
    &.alert-solid {
      // @include alert-variant($alert-background, $alert-border, $alert-color);

      --#{$prefix}alert-color: #{$alert-color};
      --#{$prefix}alert-bg: #{$alert-background};
      --#{$prefix}alert-border-color: #{$alert-border};
      --#{$prefix}alert-link-color: #{shade-color($alert-color, 20%)};
    }
  }
}

.alert-dismissible {
  .btn-close {
    border-width: 0;
  }
}
