@import "bootstrap/scss/accordion";

.accordion {
  --#{$prefix}accordion-bg: #{$white};
}

.accordion-button {

  &:not(.collapsed) {
    color: $body-color;
    background-color: $white;

    &::after {
      background-image: var(--bs-accordion-btn-icon);
    }
  }

  &::after {
    width: 14px;
    height: 14px;
    background-size: 14px;
  }
}
