.nav-main-custom-item {
  color: $body-color;
}

@include media-breakpoint-down(lg) {
  .nav-main {
    display: none;

    &.open {
      position: fixed;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: $nav-main-z-index;
      display: block;
      min-height: 100vh;
      overflow-x: hidden;
      overflow-y: auto;
      background-color: $identity-secondary-80;
    }
  }

  .nav-main-container {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    min-height: 100vh;
  }

  .nav-main-secondary {
    display: flex;
    flex: 1;
    flex-direction: column;
    gap: $nav-main-spacing-md;
    padding: 0;
    margin: 0;
    list-style-type: none;
  }

  .nav-main-secondary-item {
    order: 2;

    &.active {
      flex-grow: 1;
      order: 1;
      padding-top: $nav-main-mobile-height + 2 * $nav-main-mobile-padding;

      .nav-main-secondary-link {
        padding: $nav-main-spacing-md;
      }

      .nav-main-primary-container {
        display: block;
      }
    }
  }

  .nav-main-secondary-link {
    display: block;
    padding: 0 $nav-main-spacing-md;
    color: $white;
    text-decoration: none;

    &:hover {
      text-decoration: none;
    }

    &:active {
      color: $white;
    }
  }

  .nav-main-utils {
    display: flex;
    flex-direction: column;
    gap: $nav-main-spacing-md;
    padding: $nav-main-spacing-md 0 4rem;
    margin: 0;
    list-style-type: none;
  }

  .nav-main-utils-link {
    padding-left: $nav-main-spacing-md;
    color: $white;
    text-decoration: none;

    &:hover {
      text-decoration: none;
    }

    &:active {
      color: $white;
    }
  }

  .nav-main-primary {
    padding: 0;
    margin: 0;
    list-style-type: none;
  }

  .nav-main-primary-container {
    display: none;
    padding: 0;
  }

  .nav-main-primary-item {
    padding: 0 $nav-main-spacing-md;

    &.open {
      background-color: $nav-main-mobile-bg-darken;

      .nav-main-primary-link {
        border-bottom: 0;
      }

      .nav-main-divider,
      +.nav-main-primary-item .nav-main-divider {
        display: none;
      }

      .nav-main-sub-menu {
        max-height: fit-content;
      }
    }
  }

  .nav-main-primary-link {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: $nav-main-spacing-sm 0;
    font-size: 21px;
    color: $white;
    text-decoration: none;
    background-color: transparent;
    border: unset;

    &:hover {
      text-decoration: none;
    }

    &:active {
      color: $white;
    }
  }

  .nav-main-primary-custom-container {
    display: none;
  }

  .nav-main-site-id {
    display: none;
  }

  .nav-main-sub-menu {
    max-height: 0;
    overflow: hidden;
  }

  .nav-main-sub-menu-container {
    padding: $nav-main-spacing-lg $nav-main-spacing-md $nav-main-spacing-sm;
  }

  .nav-main-sub-menu-heading {
    margin-bottom: 0;
    font-family: $font-family-semibold;
    font-size: $font-size-base;
    color: $white;
  }

  .nav-main-sub-menu-items {
    display: flex;
    flex-direction: column;
    gap: $nav-main-spacing-md;

    padding: $nav-main-spacing-lg 0 $nav-main-spacing-lg $nav-main-spacing-md;
    margin: 0;
    list-style-type: none;
  }

  .nav-main-sub-menu-item-icon {
    display: none;
  }

  .nav-main-sub-menu-link {
    color: $white;
    text-decoration: none;

    &:active {
      color: $white;
    }
  }

  .nav-main-divider {
    height: 1px;
    margin: 0;
    background-color: $nav-main-mobile-divider-bg;
    border: 0;
    opacity: 1;
  }

  .nav-main-mobile {
    position: sticky;
    top: 0;
    left: 0;
    z-index: $nav-main-mobile-z-index;
    padding: $nav-main-mobile-padding;

    &.open {
      position: fixed;
      width: 100%;

      .nav-main-mobile-open-btn {
        display: none;
      }

      .nav-main-mobile-close-btn {
        display: inline-block;
      }
    }
  }

  .nav-main-mobile-open-btn {
    display: inline-block;
  }

  .nav-main-mobile-close-btn {
    display: none;
  }

  .nav-main-mobile-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: $nav-main-mobile-height;
    padding: $nav-main-spacing-xs;
    background-color: $white;
    @include border-radius(8px);

    .nav-main-site-id {
      display: block;

      img {
        height: $nav-main-mobile-height - 2 * $nav-main-mobile-padding;
      }
    }
  }
}

@include media-breakpoint-up(lg) {
  .nav-main {
    height: $nav-main-secondary-height;
    margin-bottom: $nav-main-primary-height + 2 * $nav-main-primary-margin;
    background-color: $dark;
  }

  .nav-main-mobile {
    display: none;
  }

  .nav-main-container {
    position: relative;
    display: flex;
    justify-content: space-between;
    max-width: 960px;
    height: 100%;
    padding: 0 $nav-main-spacing-xs;
    margin-right: auto;
    margin-left: auto;
  }

  .nav-main-secondary {
    display: flex;
    gap: $nav-main-spacing-md;
    height: 100%;
    padding: 0;
    margin: 0;
    list-style-type: none;
  }

  .nav-main-secondary-item {
    display: flex;
    align-items: center;
    height: 100%;

    &.active {
      .nav-main-secondary-link::after {
        position: absolute;
        bottom: 0;
        left: 50%;
        display: block;
        width: 0;
        height: 0;
        content: "";
        border-color: transparent transparent $bg-alt;
        border-style: solid;
        border-width: 0 7px 7px;
        transform: translateX(-50%);
      }

      .nav-main-primary-container {
        display: flex;
      }
    }
  }

  .nav-main-secondary-link {
    position: relative;
    display: flex;
    align-items: center;
    height: 100%;
    color: $white;
    text-decoration: none;

    &:active {
      color: $white;
    }
  }

  .nav-main-utils {
    display: flex;
    gap: $nav-main-spacing-md;
    align-items: center;
    padding: 0;
    margin: 0;
    list-style-type: none;
  }

  .nav-main-utils-link {
    color: $white;
    text-decoration: none;

    &:active {
      color: $white;
    }
  }

  .nav-main-utils-button {
    height: 32px;
  }

  .nav-main-primary {
    display: flex;
    gap: $nav-main-spacing-sm;
    align-items: center;

    height: 100%;
    padding: 0;
    margin: 0;
    list-style-type: none;
  }

  .nav-main-primary-container {
    position: absolute;
    top: $nav-main-secondary-height;
    left: 0;
    z-index: $nav-main-z-index;
    display: none;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    max-width: 960px;
    height: $nav-main-primary-height;
    padding: 0 $nav-main-primary-padding;
    margin: $nav-main-primary-margin auto;
    background-color: $white;
    box-shadow: $nav-main-box-shadow;
    @include border-radius(0 8px 8px 8px);

    &.sticky {
      position: fixed;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
    }
  }

  .nav-main-primary-custom-container {
    display: flex;
    align-items: center;
  }

  .nav-main-primary-item {
    height: 100%;

    &.open {
      .nav-main-primary-link::after {
        position: absolute;
        bottom: calc($nav-main-spacing-md * -1); // stylelint-disable-line function-disallowed-list
        left: 50%;
        display: block;
        width: 0;
        height: 0;
        content: "";
        border-color: transparent transparent $white;
        border-style: solid;
        border-width: 0 10px 10px;
        transform: translateX(-50%);
      }

      .nav-main-sub-menu {
        display: block;
      }
    }

    &.active {
      .nav-main-primary-link {
        font-family: $font-family-semibold;
      }
    }
  }

  .nav-main-primary-link {
    position: relative;
    display: flex;
    gap: 8px;
    align-items: center;
    height: 100%;
    color: $body-color;
    text-decoration: none;
    background-color: transparent;
    border: unset;

    &:hover {
      color: $secondary;
      text-decoration: none;
    }

    &:active  {
      color: $primary;
      text-decoration: none;
    }
  }

  .nav-main-site-id {
    margin-right: 40px;

    img {
      height: $nav-main-primary-height - 2 * $nav-main-primary-padding;
    }
  }

  .nav-main-sub-menu {
    position: absolute;
    top: $nav-main-primary-height;
    left: 0;
    display: none;
    width: 100%;
    padding: 80px 96px;
    margin-top: $nav-main-spacing-md;
    background-color: $white;
    box-shadow: $nav-main-box-shadow;
    @include border-radius(0 16px 16px 16px);
  }

  .nav-main-sub-menu-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    row-gap: 40px;
    column-gap: 56px;
  }

  .nav-main-sub-menu-heading {
    grid-row: 1;
  }

  .nav-main-sub-menu-item-icon {
    margin-right: 8px;
  }

  .nav-main-sub-menu-link {
    color: $body-color;
    text-decoration: none;

    &:hover {
      color: $identity-secondary-80;
      text-decoration: underline;
    }

    &:active {
      color: $secondary;
      text-decoration: underline;
    }
  }

  .nav-main-sub-menu-items {
    display: flex;
    flex-direction: column;
    grid-row: 2;
    gap: $nav-main-spacing-sm;
    padding: 0;
    margin: 0;
    list-style-type: none;

    >li {
      display: flex;
    }
  }

  .nav-main-divider {
    display: none;
  }
}

@include media-breakpoint-up(xl) {
  .nav-main-container,
  .nav-main-primary-container {
    max-width: 1140px;
  }
}

@include media-breakpoint-up(xxl) {
  .nav-main-primary {
    gap: $nav-main-spacing-lg;
  }

  .nav-main-container,
  .nav-main-primary-container {
    max-width: 1320px;
  }

  .nav-main-primary-link {
    font-size: 1.125rem;
  }
}
