.main-nav,
.main-nav-mobile {
  .main-nav-site-id {
    img {
      height: 48px;
    }
  }
}

.main-nav {
  z-index: $zindex-main-nav;

  // XS and SM
  @include media-breakpoint-down(lg) {
    position: fixed;
    top: 0;
    right: 100%;
    left: -80%;
    display: flex;
    flex-direction: column;
    height: 100vh;
    padding-bottom: 50px;
    overflow: auto;
    background-color: #005576;

    &.open {
      right: 20%;
      left: 0;

      .main-nav-segments > .active {
        right: 0;
        left: 0;
      }
    }

    .main-nav-level-1 {
      order: 1;
    }

    .container {
      min-width: 100%;
      padding: 0;
      margin: 0;
    }

    .main-nav-segments,
    .main-nav-utils,
    .main-nav-mobile-sections,
    .main-nav-mobile-sections ul {
      padding: 0;
      margin: 0;
      list-style-type: none;

      .main-nav-item-icon {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        width: 50px;
        padding: 15px;
        text-align: center;
        pointer-events: none;
      }
    }

    .main-nav-sections {
      display: none;
    }

    .main-nav-segments-item {
      position: relative;
      background-color: #00688e;

      &.active {
        position: absolute;
        top: 0;
        right: 100%;
        left: -80%;
      }

      > a {
        display: block;
        padding: 15px;
        font-size: 1.13em;
        color: $white;
        text-decoration: none;
      }

      .main-nav-item-icon {
        font-size: 1.13em;
        color: $white;
      }
    }

    .main-nav-utils {
      > li {
        position: relative;
        background-color: #00749e;

        > a {
          display: block;
          padding: 15px;
          font-size: 1.13em;
          color: $white;
          text-decoration: none;
        }

        .main-nav-item-icon {
          font-size: 1.13em;
          color: $white;
        }
      }
    }

    .main-nav-mobile-sections {
      padding-top: 56px;

      .main-nav-mobile-sections-item {
        position: relative;
        color: $white;
        background-color: #005c7f;

        &.expanded,
        &.collapsed {
          > a {
            padding-right: 65px;
          }
        }

        &.expanded {
          > ul {
            max-height: 100vh;
          }
        }

        > a {
          display: block;
          padding: 15px 50px 15px 30px;
          color: $white;
          text-decoration: none;
        }

        .main-nav-item-icon,
        .sub-menu-toggle {
          color: $white;
        }

        .sub-menu-toggle {
          position: absolute;
          top: 0;
          right: 0;
          bottom: 0;
          width: 50px;
          padding: 15px;
          text-align: center;
          cursor: pointer;
        }
      }
    }

    // sub-menu
    .main-nav-mobile-sections-submenu {
      position: relative;
      max-height: 0;
      overflow: hidden;

      > li {
        position: relative;
        background-color: #005576;

        > a {
          display: block;
          padding: 15px 50px 15px 45px;
          color: $white;
          text-decoration: none;
        }

        .main-nav-item-icon {
          color: $white;
        }
      }
    }

    .main-nav-site-id {
      display: none;
    }
    .main-nav-search {
      display: none;
    }
  }
  // MD and LG
  @include media-breakpoint-up(lg) {

    &.sticky {
      position: fixed;
      top: 0;
      width: 100%;

      .main-nav-level-1 {
        display: none;
      }
    }

    .main-nav-level-1 {
      background-color: $dark;

      .main-nav-container {
        display: flex;
        justify-content: space-between;
        min-height: 37px;
      }
    }

    .main-nav-sections,
    .main-nav-segments,
    .main-nav-utils {
      padding: 0;
      list-style-type: none;

      .main-nav-item-icon {
        display: none;
      }
    }

    .main-nav-mobile-sections {
      display: none;
    }

    .main-nav-segments,
    .main-nav-utils {
      display: flex;
      gap: 1.5rem;
      margin: 0;

      > li {
        cursor: pointer;

        > a {
          font-family: "Proxima Nova Light", Verdana, sans-serif;
          color: $white;
          text-decoration: none;
        }

        > a:hover {
          text-decoration: underline;
        }
      }
    }
    .main-nav-segments-item {
      position: relative;
      font-family: "Proxima Nova Light", Verdana, sans-serif;

      &.active::before {
        position: absolute;
        top: 32px;
        left: 50%;
        display: block;
        width: 0;
        height: 0;
        content: "";
        border-color: transparent transparent $bg-alt;
        border-style: solid;
        border-width: 0 7px 7px;
        transform: translateX(-50%);
      }
    }
    .main-nav-utils {
      > li {

        &:hover {
          > a {
            text-decoration: underline;
          }

          > .btn {
            text-decoration: none;
          }
        }
      }
    }

    .main-nav-level-2 {
      .main-nav-container {
        display: flex;
        justify-content: space-between;
        padding: 15px 0;
      }
    }

    .main-nav-sections {
      display: flex;
      flex-wrap: wrap;
      gap: 2rem;
      justify-content: center;
      margin: 8px 0; // auto 0 doesn't work in IE11 with flex

      .main-nav-sections-item {
        > a {
          font-family: "Proxima Nova Regular", Verdana, sans-serif;
          font-size: 16px;
          color: $dark;
          text-decoration: none;
        }

        > a:hover {
          color: $secondary;
        }

        &.active > a {
          font-family: "Proxima Nova SemiBold", Verdana, sans-serif;
        }

        .sub-menu-toggle {
          display: none;
        }
      }

      ul {
        display: none;
      }
    }
    .main-nav-search {
      .has-feedback {
        position: relative;
      }

      .form-control-feedback {
        position: absolute;
        top: 0;
        right: 8px;
        z-index: 3;
        width: 33px;
        height: 33px;
        line-height: 40px;
        color: #929292;
        text-align: center;
        cursor: pointer;
      }
    }
  }
}

.main-nav-mobile {
  position: fixed;
  top: 0;
  z-index: $zindex-main-nav-mobile;
  width: 100%;
  background-color: $white;

  @include media-breakpoint-up(lg) {
    display: none;
  }

  .main-nav-container {
    display: flex;
    justify-content: space-between;
    padding: 10px 0;
  }

  .main-nav-buttons {
    margin: auto 0;

    .main-nav-mobile-search-toggle .fa {
      line-height: 1.5;
    }
  }
}

.main-nav-mobile-search {
  max-height: 0;
  overflow: hidden;
  @include transition(max-height .2s ease-in-out);

  &.open {
    max-height: 65px;
    border-top: 1px solid #eee;
  }

  .has-feedback {
    position: relative;
  }

  .form-group {
    margin-top: 15px;
    margin-right: 0;
    margin-bottom: 0; /* Liander CSS fix */
    margin-left: 15px;
  }

  .form-control-feedback {
    position: absolute;
    top: 0; /* Liander CSS fix */
    right: 0;
    width: 34px;
    height: 34px;
    line-height: 34px; /* Fix for Mopinion loading Font Awesome styles for a second time */
    color: $gray-600;
    text-align: center;

    &:hover {
      text-decoration: none;
    }
  }

  .form-text {
    font-size: 14px;
  }
}

.main-nav-dummy {
  display: block;
  height: 66px; /* Correction for content, main-nav is fixed */

  @include media-breakpoint-up(lg) {
    display: none;
    height: 142px; /* Correction for content, when header becomes fixed */

    &.sticky {
      display: block;
    }
  }
}

.main-nav-overlay {
  @include media-breakpoint-down(lg) {
    position: fixed;
    right: 0;
    left: 0;
    z-index: $zindex-main-nav-overlay;
    height: 100vh;
    background-color: rgba(0, 0, 0, .3);
  }

  &.hidden {
    display: none;
  }
}
