@import "bootstrap/scss/buttons";

//
// Bootstrap overrides
//

.btn {
  @include border-top-start-radius(0);

  &:focus-visible {
    outline: 1px dashed inherit;
    outline-offset: 4px;
  }
}

@each $color, $value in $theme-colors {
  .btn-#{$color} {
    :not(.btn-check) + &:active {
      border-color: $btn-border-color;
    }

    &:focus-visible {
      outline: 1px dashed $value;
      outline-offset: 4px;
    }
  }

  .btn-outline-#{$color} {
    &:focus-visible {
      outline: 1px dashed $value;
      outline-offset: 4px;
    }
  }
}

.btn-tertiary {
  :not(.btn-check) + &:active {
    color: $white;
    background-color: shade-color($tertiary, $btn-active-bg-shade-amount);
  }
}

// Variant overrides
.btn-light {
  border: $input-btn-border-width solid $gray-300;
}

$buttons-override-contrast: (
  "secondary":  $secondary,
  "success":    $success,
  "info":       $info,
  "warning":    $warning,
);

@each $color, $value in $buttons-override-contrast {
  .btn-#{$color} {
    @include button-variant($value, $value, $color: $white, $hover-color: $white, $active-color: $white);
  }
}

@each $color, $value in $buttons-override-contrast {
  .btn-outline-#{$color} {
    @include button-outline-variant($value, $color-hover: $white, $active-color: $white);
  }
}


//
// Bootstrap Extensions
//

.btn-default {
  @include button-variant(
    $white,
    $gray-300,
    $color: $body-color,
  );

  &:focus-visible {
    outline: 1px dashed $gray-300;
    outline-offset: 4px;
  }
}

// Ghost Buttons

.btn-ghost {
  @include button-variant(
    $white,
    $body-color,
    $body-color,
    $body-color,
    $body-color,
    $white,
    $body-color,
    $tertiary,
    $white,
  );

  &:focus-visible {
    outline: 1px dashed $body-color;
    outline-offset: 4px;
  }
}

.btn-ghost-invert {
  @include button-variant(
    $primary,
    $white,
    $white,
    $white,
    $white,
    $body-color,
    $white,
    $tertiary,
    $body-color,
  );

  &:focus-visible {
    outline: 1px dashed $white;
    outline-offset: 4px;
  }
}

// Icon Buttons

.btn-icon-xs {
  width: $btn-icon-size-xs;
  min-width: $btn-icon-size-xs;
  height: $btn-icon-size-xs;
  min-height: $btn-icon-size-xs;
  padding: $btn-icon-padding-xs;
  font-size: $btn-icon-font-size-xs;
}

.btn-icon-sm {
  width: $btn-icon-size-sm;
  min-width: $btn-icon-size-sm;
  height: $btn-icon-size-sm;
  min-height: $btn-icon-size-sm;
  padding: $btn-icon-padding-sm;
  font-size: $btn-icon-font-size-sm;
}

.btn-icon-md {
  width: $btn-icon-size-md;
  min-width: $btn-icon-size-md;
  height: $btn-icon-size-md;
  min-height: $btn-icon-size-md;
  padding: $btn-icon-padding-md;
  font-size: $btn-icon-font-size-md;
}

.btn-icon-lg {
  width: $btn-icon-size-lg;
  min-width: $btn-icon-size-lg;
  height: $btn-icon-size-lg;
  min-height: $btn-icon-size-lg;
  padding: $btn-icon-padding-lg;
  font-size: $btn-icon-font-size-lg;
}

.btn-icon-xl {
  width: $btn-icon-size-xl;
  min-width: $btn-icon-size-xl;
  height: $btn-icon-size-xl;
  min-height: $btn-icon-size-xl;
  padding: $btn-icon-padding-xl;
  font-size: $btn-icon-font-size-xl;
}

.btn-icon-default {
  @include button-variant(
    $white,
    $white,
    $body-color,
    shade-color($secondary, $btn-hover-bg-shade-amount),
    shade-color($secondary, $btn-hover-bg-shade-amount),
    $white,
    $secondary,
    $secondary,
    $white,
  );
}

.btn-icon-ghost-on-light {
  @include button-variant(
    transparent,
    $body-color,
    $body-color,
    shade-color($secondary, $btn-hover-bg-shade-amount),
    shade-color($secondary, $btn-hover-bg-shade-amount),
    $white,
    shade-color($secondary, $btn-hover-bg-shade-amount),
    shade-color($secondary, $btn-hover-bg-shade-amount),
    $white,
  );
}

.btn-icon-ghost-on-dark {
  @include button-variant(
    transparent,
    $white,
    $white,
    $tertiary,
    $tertiary,
    $body-color,
    $tertiary,
    $tertiary,
    $body-color,
  );
}

.btn-icon-primary {
  @include button-variant(
    $primary,
    $primary,
    $white,
    $active-border: $tertiary,
  );
}

.btn-icon-secondary {
  @include button-variant(
    $secondary,
    $secondary,
    $white,
    $active-border: $tertiary,
  );
}

// Step Buttons

.btn-step {
  width: $btn-step-size;
  min-width: $btn-step-size;
  height: $btn-step-size;
  min-height: $btn-step-size;
  padding: $btn-step-padding;
  font-family: $btn-step-font-family;
  font-size: $btn-step-font-size;

  @include button-variant(
    $white,
    $body-color,
    $body-color,
    shade-color($primary, $btn-hover-bg-shade-amount),
    shade-color($primary, $btn-hover-bg-shade-amount),
    $white,
    $primary,
    $primary,
    $white,
  );
}

.btn-step-success {
  @include button-variant(
    $success,
    $success,
    $white,
    $success,
    $success,
    $white,
    $success,
    $success,
    $white,
  );
}

.btn-step-sm {
  width: $btn-step-size-sm;
  min-width: $btn-step-size-sm;
  height: $btn-step-size-sm;
  min-height: $btn-step-size-sm;
  padding: $btn-step-padding-sm;
  font-size: $btn-step-font-size-sm;
}

// Pulsating

.btn-pulsate {
  position: relative;
}

.btn-pulsate::before {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: inherit;
  height: inherit;
  content: "";
  background-color: $primary;
  @include border-radius(50%);
  opacity: .2;
  animation: pulsate 5s ease-in-out infinite;
}

@keyframes pulsate {
  0% {
    transform: scale(.1);
  }

  50% {
    transform: scale(2);
  }

  100% {
    transform: scale(.1);
  }
}
