.step-navigation-legacy {
  box-sizing: content-box;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-evenly;
  width: 100%;

  .step-navigation-item {
    position: relative;
    display: inline-block;
    padding: .375rem .75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    user-select: none;
    background-color: #fff;
    border: 1px solid rgba(0, 0, 0, .125);
    border-right: 0;
    // transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
    @include transition(
      color .15s ease-in-out,
      background-color .15s ease-in-out,
      border-color .15s ease-in-out,
      box-shadow .15s ease-in-out
    );

    &:last-child {
      border-right: 1px solid rgba(0, 0, 0, .125);
      @include border-radius(0 .25rem .25rem 0);
    }

    &:first-child {
      @include border-radius(0 0 0 .25rem);
    }

    //** Triangles **//
    &::after,
    &::before {
      /*
      &::after is the triangle after each item
      &::before is the border separator between two items
      */
      position: absolute;
      top: 0;
      left: 100%;
      width: 0;
      height: 0;
      content: "";

      /* 36px is the height of the element */
      border: solid transparent;
      border-width: 18px 0 18px 15px;
    }

    &::before {
      /* this is the border between two items */
      z-index: 1;

      /* reset style */
      margin: 0;

      border-left-color: rgba(0, 0, 0, .125);
      transform: translateX(1px);
    }

    &::after {
      z-index: 2;
      border-left-color: #fff;
    }

    &:last-child::after,
    &:last-child::before {
      display: none;
    }

    //** Active states **//
    &.active {
      color: #fff;
      border-right: 0;
    }

    &.active::before {
      transform: translateX(1px);
    }

    //** Visited state **//
    &.visited {
      color: #6c767d;
      background-color: #eee;
      border-right: 0;

      &::after {
        border-left-color: #eee;
      }

      &::before {
        border-left-color: rgba(0, 0, 0, .125);
      }
    }

    &.visited:last-child {
      &::before {
        border-left-color: #eee;
      }
    }

    //** Item width based on number of items **//

    /* one item */
    &:nth-child(1):nth-last-child(1) {
      width: 100%;
    }

    /* two items */
    &:nth-child(1):nth-last-child(2),
    &:nth-child(2):nth-last-child(1) {
      width: 50%;
    }

    /* three items */
    &:nth-child(1):nth-last-child(3),
    &:nth-child(2):nth-last-child(2),
    &:nth-child(3):nth-last-child(1) {
      width: 33.3333%;
    }

    /* four items */
    &:nth-child(1):nth-last-child(4),
    &:nth-child(2):nth-last-child(3),
    &:nth-child(3):nth-last-child(2),
    &:nth-child(4):nth-last-child(1) {
      width: 25%;
    }

    /* five items */
    &:nth-child(1):nth-last-child(5),
    &:nth-child(2):nth-last-child(4),
    &:nth-child(3):nth-last-child(3),
    &:nth-child(4):nth-last-child(2),
    &:nth-child(5):nth-last-child(1) {
      width: 20%;
    }

    /* six items */
    &:nth-child(1):nth-last-child(6),
    &:nth-child(2):nth-last-child(5),
    &:nth-child(3):nth-last-child(4),
    &:nth-child(4):nth-last-child(3),
    &:nth-child(5):nth-last-child(2),
    &:nth-child(6):nth-last-child(1) {
      width: 16.66666%;
    }

    .step-navigation-label {
      white-space: nowrap;

      @media (max-width: 767px) {
        display: none;
      }
    }
  }

  //** Colours **//
  &.step-navigation-primary > .step-navigation-item.active {
    background-color: #821e7d;
    border-color: #821e7d;
  }

  &.step-navigation-primary > .step-navigation-item.active::after,
  &.step-navigation-primary > .step-navigation-item.active::before {
    border-left-color: #821e7d;
  }

  &.step-navigation-secondary > .step-navigation-item.active {
    background-color: #008cbe;
    border-color: #008cbe;
  }

  &.step-navigation-secondary > .step-navigation-item.active::after,
  &.step-navigation-secondary > .step-navigation-item.active::before {
    border-left-color: #008cbe;
  }

  &.step-navigation-tertiary > .step-navigation-item.active {
    background-color: #322882;
    border-color: #322882;
  }

  &.step-navigation-tertiary > .step-navigation-item.active::after,
  &.step-navigation-tertiary > .step-navigation-item.active::before {
    border-left-color: #322882;
  }

  &.step-navigation-success > .step-navigation-item.active {
    background-color: #7db239;
    border-color: #7db239;
  }

  &.step-navigation-success > .step-navigation-item.active::after,
  &.step-navigation-success > .step-navigation-item.active::before {
    border-left-color: #7db239;
  }

  &.step-navigation-warning > .active {
    background-color: #e56c00;
    border-color: #e56c00;
  }

  &.step-navigation-warning > .active::after,
  &.step-navigation-warning > .active::before {
    border-left-color: #e56c00;
  }
}
