@import "../mixins/border-radius";

//
// Bootstrap overrides
//

// background extension
.bg-alt {
  background-color: $bg-alt !important; // stylelint-disable-line declaration-no-important
}

.bg-dark-transparent-30 {
  background-color: $bg-dark-transparent-30 !important; // stylelint-disable-line declaration-no-important
}

.bg-light-transparent-80 {
  background-color: $bg-light-transparent-80 !important; // stylelint-disable-line declaration-no-important
}

.bg-light-transparent-30 {
  background-color: $bg-light-transparent-30 !important; // stylelint-disable-line declaration-no-important
}

// Corperate Identity Primary Colors
@each $index, $color in $identity-primary {
  .bg-primary-#{$index} {
    background-color: $color !important; // stylelint-disable-line declaration-no-important
  }
}

// Corperate Identity Secondary Colors
@each $index, $color in $identity-secondary {
  .bg-secondary-#{$index} {
    background-color: $color !important; // stylelint-disable-line declaration-no-important
  }
}

// Corperate Identity Tertiary Colors
@each $index, $color in $identity-tertiary {
  .bg-tertiary-#{$index} {
    background-color: $color !important; // stylelint-disable-line declaration-no-important
  }
}

// border extension
.rounded-top-start-0 {
  @include border-top-start-radius(0 !important);
}

// clickable extension
.clickable {
  cursor: pointer;
}

.non-clickable {
  cursor: default;
}
