.numbered-list {
  padding-left: 0;
  list-style-type: none;
  counter-reset: li-counter;

  > li {
    position: relative;
    padding-top: 2px;
    padding-bottom: 2px;
    padding-left: 44px;

    &:last-child {
      padding-bottom: 0;
    }

    &::before {
      position: absolute;
      top: 2px;
      left: 0;
      width: 30px;
      margin-top: -2px;
      font-size: $font-size-base;
      line-height: 30px;
      color: #fff;
      text-align: center;
      content: counter(li-counter);
      counter-increment: li-counter;
      background: $primary;
      @include border-radius(100%);
    }
  }

  > p {
    display: none;
  }
}

.numbered-list-connected {
  > li::before {
    z-index: 1;
  }

  > li::after {
    position: absolute;
    top: 0;
    left: .94em;
    z-index: 0;
    width: 1px;
    height: 100%;
    content: "";
    background-image: repeating-linear-gradient(180deg, transparent, transparent 3px, $primary 3px, $primary 6px);
  }

  > li:last-child::after {
    display: none;
  }
}

.numbered-list-lg {
  > li {
    padding-top: .375rem;
    padding-bottom: 2rem;
    padding-left: 5rem;

    &::before {
      width: 3rem;
      height: 3rem;
      font-size: 1.125rem;
      line-height: 3rem;
    }
  }

  > li::after {
    left: 24px;
  }
}
