// Variables
//
// Variables should follow the `$component-state-property-size` formula for
// consistent naming. Ex: $nav-link-disabled-color and $modal-content-box-shadow-xs.

// NOTE: All active (not commented) variables with !default have the same value as in Bootstrap's variables.scss.
//       They just need to be defined, because they are being used in expressions in Liander Bootstrap's variables.scss

// Color utils
// https://mdigi.tools/darken-color/#0079a3

// Color system

$white:    #fff !default;
$gray-100: #f7f7f7;
$gray-200: #eee;
$gray-300: #d7d7d7;
$gray-400: #ced4da !default;
// $gray-500: #adb5bd !default;
$gray-600: #929292;
$gray-700: #525151;
// $gray-800: #343a40 !default;
$gray-900: #2b2827;
$black:    #000 !default;

$blue:       #0079a3;
$blue-light: #5bc0de;
$blue-dark:  #322882;
// $indigo:  #322882 !default;
$purple:     #821e7d;
// $pink:    #d63384 !default;
$red:        #a30000;
$orange:     #e56c00;
$orange-light: #fa971e;
// $yellow:  #ffc107 !default;
$green:      #7db43c;
// $teal:    #20c997 !default;
// $cyan:    #0dcaf0 !default;
$off-white:  #faf4f0;

$purple-700: shade-color($purple, 40%) !default;

$primary:       $purple;
$secondary:     $blue;
$tertiary:      $orange-light;
$success:       $green !default;
$info:          $blue-dark;
$warning:       $orange;
$danger:        $red !default;
$light:         $white;
$dark:          $gray-900;

$theme-colors: (
  "primary":    $primary,
  "secondary":  $secondary,
  "tertiary":   $tertiary,
  "success":    $success,
  "info":       $info,
  "warning":    $warning,
  "danger":     $danger,
  "light":      $light,
  "dark":       $dark
);

$identity-primary-10: #ecddec;
$identity-primary-50: #821e7d;
$identity-primary-60: #6c1968;
$identity-primary-70: #5c1559;
$identity-primary-80: #4f124c;

$identity-primary: (
  10: $identity-primary-10,
  50: $identity-primary-50,
  60: $identity-primary-60,
  70: $identity-primary-70,
  80: $identity-primary-80,
);

$identity-secondary-10: #d9ecf3;
$identity-secondary-50: #0079a3;
$identity-secondary-60: #00688e;
$identity-secondary-70: #005c7f;
$identity-secondary-80: #005576;
$identity-secondary-90: #005273;

$identity-secondary: (
  10: $identity-secondary-10,
  50: $identity-secondary-50,
  60: $identity-secondary-60,
  70: $identity-secondary-70,
  80: $identity-secondary-80,
  90: $identity-secondary-90,
);

$identity-tertiary-10: #f6e4cc;
$identity-tertiary-50: #fa971e;
$identity-tertiary-60: #e78917;
$identity-tertiary-70: #d87f12;
$identity-tertiary-80: #d0760f;

$identity-tertiary: (
  10: $identity-tertiary-10,
  50: $identity-tertiary-50,
  60: $identity-tertiary-60,
  70: $identity-tertiary-70,
  80: $identity-tertiary-80,
);


// Options
$enable-shadows: true;


// Spacing
$spacer: 1rem !default;
$spacers: (
  0: 0,
  1: $spacer * .25,
  2: $spacer * .5,
  3: $spacer,
  4: $spacer * 2,
  5: $spacer * 3,
);

// Body
//
// Settings for the `<body>` element.

$body-color:                  $dark;


// Links
//
// Style anchor elements.

$link-color:                              $secondary;
$link-hover-color:                        $purple-700;
$link-hover-decoration:                   underline;
$link-active-color:                       $purple;


// Components
//
// Define common padding and border radius sizes and more.

$border-width:                1px !default;

$border-radius:               .375rem !default;
$border-radius-sm:            .25rem !default;
$border-radius-lg:            .5rem !default;
$border-radius-xl:            1rem !default;

$box-shadow-sm:               0 .125rem .25rem rgba($black, .075) !default;
$box-shadow-inset:            inset 0 1px 2px rgba($black, .075) !default;

$component-active-bg:         $primary;


// Typography
//
// Font, line-height, and color for body text, headings, and more.

$font-size-base:              1rem !default; // Assumes the browser default, typically `16px`

$font-size-md:                $font-size-base * 1.125;    // 18px

$font-weight-lighter:         lighter !default;

$line-height-base:            1.5 !default;

// stylelint-disable value-keyword-case
$font-family-regular:         "Proxima Nova Regular", Verdana, Arial;
$font-family-light:           "Proxima Nova Light", Verdana, Arial;
$font-family-bold:            "Proxima Nova Bold", Verdana, Arial;
$font-family-semibold:        "Proxima Nova SemiBold", Verdana, Arial;
$font-family-base:            $font-family-regular;
// stylelint-enable value-keyword-case

$font-weight-normal:          400 !default;

$font-weight-base:            $font-weight-normal !default;

$h1-font-size:                $font-size-base * 3;        // 48px
$h2-font-size:                $font-size-base * 2.5;      // 40px
$h3-font-size:                $font-size-base * 1.5;      // 24px
$h4-font-size:                $font-size-base * 1.125;    // 18px
$h5-font-size:                $h4-font-size;              // 18px
$h6-font-size:                $h4-font-size;              // 18px

$headings-font-family:        $font-family-regular;
$headings-font-weight:        400;
// $headings-color:              $primary;


// Tables
//
// Customizes the `.table` component with basic values, each used across all table variations.
$table-bg:                    transparent;
$table-th-font-weight:        $font-weight-lighter;


// Custom
$table-th-font-size:          $font-size-md;


// Buttons + Forms
//
// Shared variables that are reassigned to `$input-` and `$btn-` specific variables.

$input-btn-padding-y:         .5rem !default;
$input-btn-padding-x:         1rem !default;
$input-btn-font-family:       null !default;
$input-btn-line-height:       $line-height-base !default;

$input-btn-focus-width:       0;

$input-btn-border-width:      $border-width !default;


// Buttons
//
// For each of Bootstrap's buttons, define text, background, and border color.

$btn-padding-y:               .625rem;
$btn-padding-x:               1rem;
$btn-font-size:               1rem;

$btn-padding-y-sm:            .375rem;
$btn-padding-x-sm:            1rem;
$btn-font-size-sm:            1rem;

$btn-padding-y-lg:            .75rem;
$btn-padding-x-lg:            1.5rem;
$btn-font-size-lg:            1.125rem;

$btn-border-radius:           .5rem;
$btn-border-radius-sm:        .5rem;
$btn-border-radius-lg:        1rem;

$btn-box-shadow:              none;
$btn-border-color:                $gray-900;

$btn-hover-bg-shade-amount:       30%;
$btn-hover-bg-tint-amount:        30%;
$btn-hover-border-shade-amount:   30%;
$btn-hover-border-tint-amount:    30%;
$btn-active-bg-shade-amount:      30%;
$btn-active-bg-tint-amount:       30%;

// Icon Buttons - Possible sizes?
// Size:    xl, lg, md, sm, xs
// Desktop: 64, 56, 48, 40, 32

$btn-icon-size-xs:                2rem;
$btn-icon-padding-xs:             .5rem;
$btn-icon-font-size-xs:           $font-size-base * .625;

$btn-icon-size-sm:                2.5rem;
$btn-icon-padding-sm:             .5rem;
$btn-icon-font-size-sm:           $font-size-base * .75;

$btn-icon-size-md:                3rem;
$btn-icon-padding-md:             .625rem;
$btn-icon-font-size-md:           $font-size-base;

$btn-icon-size-lg:                3.5rem;
$btn-icon-padding-lg:             .75rem;
$btn-icon-font-size-lg:           $font-size-base * 1.25;

$btn-icon-size-xl:                4rem;
$btn-icon-padding-xl:             .875rem;
$btn-icon-font-size-xl:           $font-size-base * 1.5;

// Step Buttons
$btn-step-size:                   3rem;
$btn-step-padding:                .625rem;
$btn-step-font-family:            $font-family-semibold;
$btn-step-font-size:              $font-size-base * 1.125;

$btn-step-size-sm:                2rem;
$btn-step-padding-sm:             .25rem;
$btn-step-font-size-sm:           $font-size-base;


// Forms
$input-padding-y:             $input-btn-padding-y !default;
$input-padding-x:             $input-btn-padding-x !default;
$input-font-weight:           $font-weight-base !default;
$input-line-height:           $input-btn-line-height !default;

$input-font-family:           $input-btn-font-family !default;

$input-bg:                    $off-white;
$input-disabled-bg:           $gray-200 !default;

$input-color:                 $body-color !default;
$input-border-color:          $body-color;
$input-border-width:          $input-btn-border-width !default;
$input-box-shadow:            $box-shadow-inset !default;

$input-border-radius:         $border-radius-sm;
$input-border-radius-sm:      $border-radius-sm;
$input-border-radius-lg:      $border-radius-sm;

$input-focus-border-color:    $input-border-color;
$input-focus-box-shadow:      none;

$input-placeholder-color:     $gray-600;

$input-height-border:         $input-border-width * 2 !default;

$input-height-inner:          add($input-line-height * 1em, $input-padding-y * 2) !default;

$input-height:                add($input-line-height * 1em, add($input-padding-y * 2, $input-height-border, false)) !default;

$form-check-input-width:      1.25em;
$form-check-margin-bottom:    .5rem;

$input-group-addon-bg:        $gray-200 !default;

$custom-file-height:                $input-height !default;
$custom-file-height-inner:          $input-height-inner !default;
$custom-file-focus-border-color:    $input-focus-border-color !default;
$custom-file-focus-box-shadow:      $input-focus-box-shadow !default;
$custom-file-disabled-bg:           $input-disabled-bg !default;

$custom-file-padding-y:             $input-padding-y !default;
$custom-file-padding-x:             $input-padding-x !default;
$custom-file-line-height:           $input-line-height !default;
$custom-file-font-family:           $input-font-family !default;
$custom-file-font-weight:           $input-font-weight !default;
$custom-file-color:                 $input-color !default;
$custom-file-bg:                    $gray-200;
$custom-file-border-width:          $input-border-width !default;
$custom-file-border-color:          $input-border-color !default;
$custom-file-border-radius:         $input-border-radius !default;
$custom-file-box-shadow:            $input-box-shadow !default;
$custom-file-button-color:          $custom-file-color !default;
$custom-file-button-bg:             $white;
$custom-file-text: (
  en: "Browse",
  nl: "Kies bestand"
);


// Form validation

$form-feedback-valid-color:         $success !default;
$form-feedback-invalid-color:       $danger !default;

$form-feedback-icon-valid-color:    $form-feedback-valid-color !default;
$form-feedback-icon-valid:          url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'><path fill='#{$form-feedback-icon-valid-color}' d='M2.3 6.73.6 4.53c-.4-1.04.46-1.4 1.1-.8l1.1 1.4 3.4-3.8c.6-.63 1.6-.27 1.2.7l-4 4.6c-.43.5-.8.4-1.1.1z'/></svg>") !default;
$form-feedback-icon-invalid:        none;

$form-validation-states: (
  "valid": (
    "color": $form-feedback-valid-color,
    "icon": $form-feedback-icon-valid
  ),
  "invalid": (
    "color": $form-feedback-invalid-color,
    "icon": $form-feedback-icon-invalid
  )
) !default;


// Popover

$popover-border-color: $white;
$popover-header-bg: $white;
$popover-box-shadow: $box-shadow-sm;


// Z-index master list
//
// Warning: Avoid customizing these values. They're used for a bird's eye view
// of components dependent on the z-axis and are designed to all work together.

$zindex-main-nav-mobile:             900;
$zindex-main-nav-overlay:            905;
$zindex-main-nav:                    910;
// $zindex-dropdown:                   1000 !default;
// $zindex-sticky:                     1020 !default;
// $zindex-fixed:                      1030 !default;
// $zindex-offcanvas-backdrop:         1040 !default;
// $zindex-offcanvas:                  1045 !default;
// $zindex-modal-backdrop:             1050 !default;
// $zindex-modal:                      1055 !default;
// $zindex-popover:                    1070 !default;
// $zindex-tooltip:                    1080 !default;
// $zindex-toast:                      1090 !default;


// Dropdowns

$dropdown-border-width:             0;
$dropdown-box-shadow:               0 .125rem .25rem rgba($black, .075);


// Pagination

$pagination-padding-y:              .75rem;
$pagination-padding-x:              1.25rem;

$pagination-color:                  $body-color;
$pagination-bg:                     transparent;
$pagination-border-color:           $body-color;

$pagination-focus-color:            $white;
$pagination-focus-bg:               $secondary;
$pagination-focus-border-color:     $secondary;

$pagination-hover-color:            $white;
$pagination-hover-bg:               $secondary;
$pagination-hover-border-color:     $secondary;

$pagination-active-color:           $white;
$pagination-active-bg:              $secondary;
$pagination-active-border-color:    $secondary;


// Cards

$card-spacer-y:                     $spacer !default;

$card-border-radius:                1rem;
$card-border-width:                 0;

$card-cap-padding-y:                $card-spacer-y * .75;


// Toasts

$toast-border-width: 0;


// Modals

$modal-inner-padding:               2rem;
$modal-content-box-shadow-xs:       none;
$modal-content-box-shadow-sm-up:    none;
$modal-content-border-width:        0;
$modal-header-border-width:         0;
$modal-footer-border-width:         0;
$modal-backdrop-opacity:            .25;


// Alerts

$alert-border-width: 0;

$alert-light-bg: #fdfaf9;


// Close

$btn-close-width:             .75em;
$btn-close-padding-x:        1em;


// Offcanvas

$offcanvas-bg-color: $primary;
$offcanvas-color: $white;
$offcanvas-horizontal-width: 500px;


// Utilities

$bg-alt:                      $off-white;
$bg-dark-transparent-30:      rgba($black, .3);
$bg-light-transparent-80:     rgba($white, .8);
$bg-light-transparent-30:     rgba($white, .3);


// Footer
//
// Footer navigation
$footer-padding:              5rem;
$footer-padding-small:        2.5rem;


// Livechat
//
// Livechat
$livechat-bg: 	              $white;
$livechat-border-radius:      $border-radius;
$livechat-margin:             1rem;
$livechat-max-width:          395px;
$livechat-max-height:         600px;

$livechat-header-padding-y:   .25rem;
$livechat-header-padding-x:   1rem;


// Nav Main
//
// Main Navigation
$nav-main-spacing-xs:         .75rem;
$nav-main-spacing-sm:         1rem;
$nav-main-spacing-md:         1.5rem;
$nav-main-spacing-lg:         2rem;

$nav-main-box-shadow:         rgba(0, 0, 0, .075) 0 2px 4px 0;

$nav-main-z-index:            900;
$nav-main-primary-height:     5rem;
$nav-main-primary-padding:    $nav-main-spacing-sm;
$nav-main-primary-margin:     $nav-main-spacing-sm;
$nav-main-secondary-height:   3rem;

$nav-main-mobile-height:      4rem;
$nav-main-mobile-padding:     $nav-main-spacing-xs;
$nav-main-mobile-z-index:     901;
$nav-main-mobile-bg-darken:   rgba(43, 40, 39, .19);
$nav-main-mobile-divider-bg:  rgba(255, 255, 255, .19);


// Process
//
// Process Overview
$process-bg:                      $bg-alt;

$process-line-color:              $primary;
$process-line-width:              1px;
$process-line-container-width:    3.5rem;

$process-index-number-font-size:  1rem;

$process-card-bg:                 $white;
$process-card-secondary-bg:       $secondary;
$process-card-border-color:       rgba(0, 0, 0, .1);
$process-card-border-radius:      $border-radius;
$process-card-border-width:       1px;
$process-card-margin:             1rem;
$process-card-padding:            1rem;

$process-card-arrow-top:          1rem;
$process-card-arrow-width:        1rem;
$process-card-arrow-height:       2rem;
$process-card-arrow-margin:       .25rem;
$process-card-arrow-color:        $process-card-bg;
$process-card-arrow-outer-color:  rgba(0, 0, 0, .25);

$process-circle-color:            $white;
$process-circle-bg:               $primary;
$process-circle-size:             $process-card-arrow-height + ($process-card-arrow-margin * 2);
$process-circle-start-size:       $process-line-container-width;
$process-circle-last-size:        $process-circle-size + .5rem;


@import "bootstrap/scss/variables";
