// stylelint-disable @stylistic/selector-list-comma-newline-after

@import "bootstrap/scss/type";
@import "type/numbered-list";

//
// Headings
//

h1, .h1 {
  line-height: 1.1;
}

h2, .h2 {
  line-height: 1.3;
}

h3, .h3 {
  line-height: 1.4;
}

h4, .h4,
h5, .h5,
h6, .h6 {
  font-family: $font-family-semibold;
  line-height: 1.4;
}

.lead {
  font-family: $font-family-light;
}

//
// Misc
//

a:active {
  color: $link-active-color;
}

strong {
  font-family: $font-family-semibold;
}
