@import "bootstrap/scss/offcanvas";

.offcanvas-header {
  .btn-close {
    --bs-btn-close-color: $white;
    background-color: $white;
    opacity: 1;
  }
}

.offcanvas.offcanvas-start {
  width: 550px;

  &::before {
    position: absolute;
    @include border-radius(200vh);
    top: -50%;
    right: -20vh;
    z-index: -1;
    width: 200vh;
    height: 200vh;
    content: "";
    background-color: $primary;
  }
  .offcanvas-body {
    padding-right: 0;
  }
}

.offcanvas.offcanvas-end {
  width: 700px;

  &::before {
    position: absolute;
    @include border-radius(200vh);
    top: -50%;
    left: -20vh;
    z-index: -1;
    width: 200vh;
    height: 200vh;
    content: "";
    background-color: $primary;
  }
  .offcanvas-body {
    padding-right: 6rem;
  }
}
